import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	data() {
		return {
			barHeight: 25,
		}
	},
	computed: {
		...mapState(['hasLogin', 'forcedLogin','userInfo'])
	},
	mounted() {
		this.getSystemStatusBarHeight();
	},
	methods: {
		getSystemStatusBarHeight() {
			var height = plus.navigator.getStatusbarHeight();
			this.barHeight = height;
		},
		cancelLogin(){
			const _this = this;
			uni.showModal({
				title:"确定退出",
				content:"是否退出程序",
				success(data){
					if(data.confirm){
						// _this.$HTTP({
						// 	url:'UserSignOut',
						// 	data:{
								
						// 	}
						// })
						uni.reLaunch({
							url:"../login/login"
						})
					}
				}
			})
		}
	}
}
